@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
.slider{height:100vh;margin:0;font-family:"Poppins",sans-serif}.slider .slide{position:relative}.slider .linkContent{position:absolute}.slider .linkContent .titleSlide{font-size:50px;margin:10px;color:#fff;text-shadow:1px 1px 0 #22140c}.slider .swiper-slide{text-align:center;font-size:18px;display:flex;justify-content:center;align-items:center}.slider .swiper-container{width:100%;height:100%}.slider .swiper-slide img{display:block;width:100%;height:100%;object-fit:cover}
button{position:relative;display:inline-block;cursor:pointer;outline:none;border:0;vertical-align:middle;text-decoration:none;font-size:12px;font-family:inherit;opacity:.5}button.LearnMore{border:2px solid transparent;color:#382b22;text-transform:uppercase;padding:1.25em 2em;border-radius:2px;transform-style:preserve-3d;transition:transform 150ms cubic-bezier(0, 0, 0.58, 1),background 150ms cubic-bezier(0, 0, 0.58, 1)}button.LearnMore::before{position:absolute;content:"";width:100%;height:100%;top:0;left:0;right:0;bottom:0;background:transparent;border-radius:inherit;box-shadow:0 0 0 2px transparent,0 .625em 0 0 transparent;transform:translate3d(0, 0.75em, -1em);transition:transform 150ms cubic-bezier(0, 0, 0.58, 1),box-shadow 150ms cubic-bezier(0, 0, 0.58, 1)}button.LearnMore:hover{color:#fff;border:2px solid;background:rgba(180,159,158,.51)}button.LearnMore:hover::before{box-shadow:0 0 0 2px transparent,0 .5em 0 0 transparent;transform:translate3d(0, 0.5em, -1em)}button.LearnMore:active::before{box-shadow:0 0 0 2px transparent,0 0 transparent;transform:translate3d(0, 0, -1em)}
.navigation{display:flex;position:absolute;z-index:2;padding:10px;font-family:"Poppins",sans-serif;font-size:30px;text-shadow:1px 1px 0 #22140c;width:97.8%;margin:10px;justify-content:space-between}.navigation .initialPage{cursor:pointer;width:auto}.navigation .initialPage a{text-decoration:none;color:#fff}.navigation .menu{display:flex}.navigation .menu a{text-decoration:none;color:#fff;transition:.1s}.navigation .menu a:hover{color:#af7777}.navigation .privateOffice{border:1px solid green;color:#fff}
.dropdown{border:1px solid;width:400px;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin:100px auto;position:relative}.dropdown .dropdown-btn{padding:15px 20px;background:#fff;box-shadow:3px 3px 10px 6px rgba(0,0,0,.06);font-weight:bold;color:#333;display:flex;align-items:center;cursor:pointer;justify-content:space-between}.dropdown .dropdown-content{position:absolute;top:110%;left:0;padding:10px;background:#fff;box-shadow:3px 3px 10px 6px rgba(0,0,0,.06);font-weight:500;color:#333;width:95%}.dropdown .dropdown-content .dropdown-item{padding:10px;cursor:pointer;transition:all .2s}.dropdown .dropdown-content .dropdown-item:hover{background:#f4f4f4}
*{padding:0;margin:0}.container{font-family:Helvetica,sans-serif;color:#000}
